.Dashboard_card_box__qCqBY{
  position: relative;
  margin-top: 50px;
  width: 500px;
  height: 300px;
}

  
.Dashboard_overlay__2xDhC {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0);
    transition: background 0.5s ease;
}
  
  .Dashboard_card_box__qCqBY:hover .Dashboard_overlay__2xDhC {
    display: block;
    background: rgba(0, 0, 0, .3);
  }

  .Dashboard_title__3CjRb {
    position: absolute;
    width: 250px;
    left: 15px;
    top: 60px;
    font-weight: 400;
    font-size: 30px;
    text-align: center;
    text-transform: uppercase;
    color: black;
    z-index: 1;
    transition: top .5s ease;
    border:1px solid black
  }

  .Dashboard_text__1U57R {
    position: absolute;
    width: 200px;
    left: 40px;
    top: 140px;
    /* font-weight: 700; */
    font-size: 10px;
    text-align: center;
    text-transform: uppercase;
    color: black;
    z-index: 1;
    transition: top .5s ease;
  }
  
  .Dashboard_card_box__qCqBY:hover .Dashboard_title__3CjRb {
    top: 90px;
  }
  
  .Dashboard_button__2WDPj {
    position: absolute;
    width: 200px;
    left:40px;
    top: 200px;
    text-align: center;
    opacity: 0;
    transition: opacity .35s ease;
  }
  
  .Dashboard_button__2WDPj a {
    width: 200px;
    padding: 12px 48px;
    text-align: center;
    color: white;
    border: solid 2px white;
    z-index: 1;
  }
  
  .Dashboard_card_box__qCqBY:hover .Dashboard_button__2WDPj {
    opacity: 1;
  }
  
